<template>
  <div>
    <div>
      Admin Test
    </div>
  </div>
</template>

<script>
  import UserService from "../services/user.service";

  export default {
    name: "AdminTest",
    data() {
      return {
        content: null
      };
    },
    mounted() {
      UserService.getUserBoard().then(
          response => {
            this.content = response.data;
          },
          error => {
            this.content =
                (error.response && error.response.data && error.response.data.message) ||
                error.message ||
                error.toString();
          }
      );
    }
  }
</script>

